
import React from "react";
class Signin extends React.Component {
  constructor(props) {
    super();
    this.state = {
      signInEmail: '',
      signInPassword: ''
    };
  }

  onEmailChange = (event) => {
    this.setState({signInEmail: event.target.value });
  }
  onPasswordChange = (event) => {
    this.setState({signInPassword: event.target.value });
  }

onSubmitSignIn = (event) => {
  event.preventDefault();
  // Added check to ensure fields are not empty
  if (!this.state.signInEmail || !this.state.signInPassword) {
    return alert("Please enter your email and password.");
  }

  fetch('https://smartbrain-af5q.onrender.com/signin', {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify({
      email: this.state.signInEmail,
      password: this.state.signInPassword
    })
  })
    .then(response => response.json())
      .then(user =>{
        if(user.id){
          this.props.loadUser(user);
          this.props.onRouteChange('home');
        }else{
          alert('Login failed: Please check your email and password');
        }
      })
    .catch(err => {
      console.error(err);
      alert('Login failed: Please check your email and password');
    });
}

  render() {
    const { onRouteChange } = this.props;
    // Signin component renders a simple form for user sign-in
    return (
      <article className="br5 ba dark-gray b--black-10 mv4 w-100 w-50-m w-25-l mw5 shadow-5 center">
        <main className="pa4 black-80">
          <form className="measure">
    <fieldset id="sign_up" className="ba b--transparent ph0 mh0">
      <legend className="f4 fw6 ph0 mh0">Sign In</legend>
      <div className="mt3">
        <label className="db fw6 lh-copy f6" htmlFor="email-address">Email</label>
        <input className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" 
        type="email" 
        name="email-address"  
        id="email-address"
        onChange={this.onEmailChange}/>
      </div>
      <div className="mv3">
        <label className="db fw6 lh-copy f6" htmlFor="password">Password</label>
        <input className="b pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" 
        type="password" 
        name="password" 
        id="password"
        onChange={this.onPasswordChange}/>
      </div>
    </fieldset>
    <div className="">
      <input 
      onClick={this.onSubmitSignIn}
      className="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib" 
      type="submit" value="Sign in"/>
    </div>
    <div className="lh-copy mt3">
      <p onClick={()=> onRouteChange('register')} className="f6 link dim black db pointer">New? Register Here</p>
    </div>
  </form>
</main>
</article>
);
}
}
export default Signin;

